.fse-pd-post-list-wrapper {
	width: 100%;
	display: flex;
	gap: 50px;
}
.feature-pd-post-wrapper {
	width: 60%;
}
.fse-pd-right-side-post-list {
	width: 40%;
}
.fse-pd-side-grid {
	display: flex;
    gap: 20px;
    margin-bottom: 30px;
    width: 100%;
}
.fse-pd-feature-title {
	font-family: "Barlow Condensed", sans-serif;
    font-weight: 700;
    font-size: 48px;
    text-transform: uppercase;
    margin: 0px;
}
.fse-pd-side-grid-title {
	font-family: "Plus Jakarta Sans", sans-serif;
    font-weight: 800;
    font-size: 24px;
    margin-top: 10px;
    margin-bottom: 10px;
}
.fse-pd-feature-description {
	font-weight: 400;
	font-size: 18px;
	font-family: "Plus Jakarta Sans", sans-serif;
	padding: 20px 0px;
	margin: 0;
}
.fse-pd-feature-category {
	padding-top: 20px;
	padding-bottom: 10px;
	display: block;
}
.category-pd,
.fse-pd-feature-category {
	color: #006DB7;
	font-family: "Plus Jakarta Sans", sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
}
.date-pd-and-comment-wrapper {
	display: flex;
}
.date-pd-and-comment-wrapper .fse-pd-date,
.date-pd-and-comment-wrapper .fse-pd-comments,
.fse-pd-feature-metadata .fse-pd-date,
.fse-pd-feature-metadata .fse-pd-comments{
	font-family: "Plus Jakarta Sans", sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    color: #767676;
    font-weight: 700;
    margin-right: 20px;
}
.fse-pd-feature-metadata{
	display: flex;
}
.fse-pd-side-comment-count {
	margin: 0;
	margin-left: 5px;
}
.fse-pd-comments {
	display: flex;
	align-items: center;
}
.feature-pd-image{
	width: 100%;
}
.side-pd-grid-feature-image {
   width: 40%;
}
.side-pd-grid-feature-image img{
   width: 100%;
}
.fse-pd-side-metadata {
	width: 60%;
}
@media only screen and (max-width: 1320px) {
	.fse-pd-feature-title {
		font-size: 45px;
	}
}
@media only screen and (max-width: 1200px) {
	.fse-pd-feature-title {
		font-size: 42px;
	}
	.fse-pd-side-grid-title {
		font-size: 20px;
	}
}
@media only screen and (max-width: 1285px) {
	.fse-pd-feature-title {
		font-size: 40px;
	}
	.fse-pd-side-grid-title {
		font-size: 20px;
	}
}
@media only screen and (max-width: 1180px) {
	.fse-pd-feature-title {
		font-size: 35px;
	}
	.fse-pd-side-grid-title {
		font-size: 16px;
	}
}
@media only screen and (max-width: 1000px) {
	.fse-pd-post-list-wrapper {
		gap: 30px;
	}
}
@media only screen and (max-width: 986px) {
	.fse-pd-post-list-wrapper {
		gap: 30px;
	}
	.fse-pd-feature-title {
	    font-size: 30px;
	}
	.fse-pd-side-grid {
		margin-bottom: 25px;
	}
}
@media only screen and (max-width: 900px) {
	.date-pd-and-comment-wrapper .fse-pd-date,
	.date-pd-and-comment-wrapper .fse-pd-comments,
	.fse-pd-feature-metadata .fse-pd-date,
	.fse-pd-feature-metadata .fse-pd-comments {
		font-size: 11px;
	}
}
@media only screen and (max-width: 870px) {
	.fse-pd-feature-title {
		font-size: 27px;
	}
	.fse-pd-side-grid-title {
		font-size: 12px;
	}
	.fse-pd-feature-description {
		font-size: 16px;
	}
}
@media only screen and (max-width: 800px) {
	.fse-pd-post-list-wrapper {
		display: block;
	}
	.feature-pd-post-wrapper {
	    width: 100%;
	}
	.fse-pd-right-side-post-list {
	    width: 100%;
	}
	.fse-pd-right-side-post-list {
		padding-top: 20px;
	}
	.side-grid-feature-image {
		width: 20%;
	}
	.fse-pd-side-grid-title {
	    font-size: 21px;
	}
	.fse-pd-feature-title {
		font-size: 40px;
	}
}
@media only screen and (max-width: 800px) {
	.date-pd-and-comment-wrapper .fse-pd-date, .date-pd-and-comment-wrapper .fse-pd-comments, .fse-pd-feature-metadata .fse-pd-date, .fse-pd-feature-metadata .fse-pd-comments {
	    font-size: 14px;
	}
	.side-pd-grid-feature-image {
		width: 30%;
	}
	.fse-pd-side-metadata {
		width: 70%;
	}
}
@media only screen and (max-width: 400px) {
	.side-pd-grid-feature-image {
		width: 40%;
	}
	.fse-pd-side-metadata {
		width: 60%;
	}
}